.App {
  text-align: center;
}

html, body, #app,#root {
  height: 100%;
  overflow: hidden;
}

body {
  background-color: #202124;
  font-family: "Roboto",arial,sans-serif;
  font-size: 1rem;
}

.main{
  position: relative;
  width: 100%;
  height: 85%;
}

.footer{
  position: relative;
  width: 100%;
  height: 15%;
}
.image {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 150px;
  height: 150px;
  overflow: hidden;
  border-radius: 50%;
}
.logoImage {
  width: 100%; 
  height: auto;
}
.users{
  font-size: .75rem;
  color: #FFFFFF;
  position: absolute;
  left: 3%;
  bottom: 1%;
}
.time{
  color: #FFFFFF;
  margin-left: 12px;
}
.makeid{
  color: #FFFFFF;
}
.leftFooter{
  position: absolute;
  display: inline-block;
  left: 0%;
  top: 50%;
  transform: translate(-0%, -50%);
  color: #FFFFFF;
}
.leftFooter span{
  padding: 0 12px;
}
.centerFooter {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.button {
  border-radius: 50%;
  background-color: rgb(60,64,67);
  border: none;
  color: white;
  padding: 8px;
  object-fit: none;
  margin: 6px;
  height: 40px;
  width: 40px;
}

.buttonphone{
  border-radius: 30%;
  background-color: rgb(234,67,53);
  border: none;
  color: white;
  padding: 8px 20px;
  object-fit: none;
  margin: 6px;
  height: 40px;
}

.button:hover ,.buttonphone:hover{
  opacity: 0.7;
}

.rightFooter{
  position: absolute;
  right: 1%;
  top: 50%;
  color: #FFFFFF;
  transform: translate(-0%, -50%);
}
.rightFooter span{
  padding:  12px;
}